import { StatusBar } from 'expo-status-bar';
import React, {useState} from 'react';
import { StyleSheet, Text, View } from 'react-native';
import { Router, Switch, Route, Redirect, BackButton, DeepLinking } from "react-native-web-router";

import Home from './application/screens/Home';
import Dashboard from './application/screens/Dashboard';

export default function App() {
  return (
    <Router
        basename="bravo-components-web"
        theme={{
            ios: {
                tintColor: "#393",
            },
            android: {
                backgroundColor: "#393",
            },
            web: {
                backgroundColor: "#393",
                desktop: {
                    backgroundColor: "#9f9",
                },
            },
        }}
    >
        <BackButton>
            <Switch>
                <Route path={"/"} exact component={Home}/>
                <Route path={"/dashboard"} component={Dashboard}>
                  <Route path={"/home"} component={Home} />
                </Route>
                <Redirect to={"/"}/>
            </Switch>
            <DeepLinking/>
        </BackButton>
    </Router>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
